import React, { Component } from 'react';
import "../Estilos/EstilosBase.css";
import "./Home.css"
import profilePicture from "../../Assets/Img/profilePictureCompress.jpg"
import { Container } from 'react-bootstrap';
import InfoHome from '../InfoHome/InfoHome';

class Home extends Component {
    render() {
        return (
            <header className='containerHome'>

                
                <div className='home centradoHome sectionHome'>
                    <div className='HomeStyle'>
                        <img src={profilePicture} alt="" className='profilePictureStyle' />
                        <div className='containerInfoName'>
                            <div className='titleHome'>
                                ¡Hola! Me llamo
                            </div>
                            <div className='NameHome'>
                                Diaz Orlandi, Ignacio Emanuel
                            </div>
                        </div>
                    </div>
                </div>

            <div className='sectionHome'>
                <InfoHome/>
            </div>
            </header>

        );
    }
}

export default Home;